/*------------------------------------*\
    TOOLKIT.CSS
\*------------------------------------*/

/**
 * Import Variables
 */

@import "toolkit/vars"; //Custom vars that override bootstrap vars
// @import "bootstrap/bootstrap"; //Boostrap v3.3.5 straight out of the box
// @import "plugins/bootstrap-ms"; //Breakpoint in between xs and sm

/**
 * Utilities
 */

@import "toolkit/util/tk-mixins"; //custom tk mixins
@import "toolkit/util/tk-utilities"; //tk helper classes
@import "toolkit/util/bootstrap-extends"; //boostrap extends & overwritten styles

/**
 * Plugins
 */

@import "plugins/tablesaw"; //responsive tabless
@import "plugins/multiple-select";
@import "plugins/idangerous-swiper"; //deprecated swiper
@import "plugins/fullcalendar"; //montly calendar
@import "plugins/owl-carousel"; //news swiper
@import "toolkit/components/owl-carousel-custom"; //custom owl swiper
@import "plugins/ekko-lightbox"; //lightbox

/**
 * Foundations
 */

@import "toolkit/foundations/typography";
@import "toolkit/foundations/forms";
@import "toolkit/foundations/tables";
@import "toolkit/foundations/images"; //image ratio and responsive images
@import "toolkit/foundations/tk-icons"; //custom toolkit icons, file generated by gulp
@import "toolkit/foundations/tk-icons-helper"; //css for better gulp icons, wont be need when icons become a plugin

/**
 * Layout
 */

@import "toolkit/layout/containers"; //site containers and row containers
@import "toolkit/layout/wrappers"; //wrappers
@import "toolkit/layout/columns"; //page columns used with the sidebar
@import "toolkit/layout/rows"; //custom versions of BS .row grid class

/**
 * Components
 */

//Large components that make up the page layout and structure
@import "toolkit/components/masthead"; //uol masthead
@import "toolkit/components/quicklinks"; //quicklinks dropdown nav
@import "toolkit/components/local-header"; // localheaer (coloured bar)
@import "toolkit/components/site-search"; // header site search
@import "toolkit/components/quick-search"; //ajaxdropdown
@import "toolkit/components/site-footer"; //sie footer
@import "toolkit/components/sidebar"; //sidebar nav

//Nav
@import "toolkit/components/nav-priority"; //priority nav
@import "toolkit/components/navicon"; //mobile navicon button

//Components (keep alphabetical)
@import "toolkit/components/banner"; //hero banner NB not the swiper (See owl carousel)
@import "toolkit/components/breadcrumb"; //modify BS breadcrumb
@import "toolkit/components/cards"; //card pattern, flat or stacked
@import "toolkit/components/dividers"; //header divider and hr with an image
@import "toolkit/components/expander"; //expanse hidden content
@import "toolkit/components/flags"; //maybe remove and use cards
@import "toolkit/components/islands"; //boxed in content
@import "toolkit/components/lists"; //styled lists and nav
@import "toolkit/components/news-events"; // news and events widget
@import "toolkit/components/modal-iframe"; //modal thats holds an iframe (used for the crm)
@import "toolkit/components/page-contents"; //page contents list
@import "toolkit/components/page-footer"; //page footer, next & prev
@import "toolkit/components/pagination"; //modify BS version
@import "toolkit/components/profile-block"; //marketing profile block
@import "toolkit/components/responsive-accordion"; //
@import "toolkit/components/responsive-images"; //responsive images that enforce a proportion at different breakpoints
@import "toolkit/components/social-share"; //social sharing buttons
@import "toolkit/components/swiper"; //old idangerous swiper - remove when possible
@import "toolkit/components/tabs"; //custom tabs using BS JS
@import "toolkit/components/tiles"; //breaks the BS grid
@import "toolkit/components/twitter-feed"; //twitter footer and widget
@import "toolkit/components/featured-content"; //featured content widget

/**
 * Skin - Common design styles applied to elements
 */

@import "toolkit/util/tk-skin"; //skin applied to various elements (leave at bottom for specificity)

